<script>
  import Image from '~/components/image.svelte';
  import Iframe from '~/components/iframe.svelte';
  import Video from '~/components/video.svelte';
  
  import Carousel from '~/components/carousel.svelte';

  export let links = [];
  export let title = 'temp title';
  export let description = 'temp description';
  export let previews = [];
  export let year = 2020;
  export let tags = ['photoshop', 'illustrator'];
</script>

<div class="work-item item">
  <div class="content">

    <time year="{year}">{year}</time>
    <h3>{title}</h3>
    <p>{@html description}</p>

    {#if tags.length}
      <ul class="tags">
        {#each tags as tag}
          <li>{tag}</li>
        {/each}
      </ul>
    {/if}

    {#if links.length}
      <ul class="links">
        {#each links as link}
          <li><a href="{link.url}">{link.label}</a></li>
        {/each}
      </ul>
    {/if}

  </div>

  {#if previews.length}
    <div class="preview">
      <Carousel class="images">
        {#each previews as preview}
        {#if preview.type == 'image'}
          <Image src={preview.src} alt={preview.title} />
        {:else if preview.type == 'iframe'}
          <Iframe src={preview.src} />
        {:else if preview.type == 'video'}
          <Video src={preview.src} />
        {/if}
        
        {/each}
      </Carousel>
    </div>
  {/if}

</div>