import { writable, get } from 'svelte/store';

// languages
// import se from '~/lang/sv.json';
import en from '~/lang/en.json';


// themes 
export const theme = (() => {
  // const store = writable(localStorage.getItem('theme') || 'true');
  const store = writable(true);
  const { subscribe, update } = store;
  document.documentElement.dataset.theme = get(store) ? 'dark' : 'light';
  return {
    subscribe,
    toggle(){
      update((current) => {
        const val = !current;
        // localStorage.setItem('theme', !val);
        document.documentElement.dataset.theme = val ? 'dark' : 'light';
        return val;
        // return localStorage.getItem('theme');
      });
    }
  }
})();


// selected language
const [country, code] = navigator.language.toLowerCase().split('-'); 
export const defaultLanguage = code || 'en';

// words
export const languageList = new Map([
  ['en', en],
  // ['se', se]
]);

export const lang = writable(undefined);

export const setTranslation = (l) => {
  if(languageList.has(l)){
    lang.set(languageList.get(l));
  }
}

export const getTranslation = (l) => {
  if(languageList.has(l)){
    return languageList.get(l);
  }
  return languageList.get(defaultLanguage);
}

// debug with en only to start with
setTranslation('en');
// setTranslation(defaultLanguage);


// active words
export const language = writable(defaultLanguage);
export const setLanguage = (l) => {
  if(!languageList.has(l)){
    console.log('error invalid language!!');
  }
  language.set(l);
}


/*
  requestanimationframe loop
  
  @param callback (Function)
  @param fps (Number) (optional)
  
  @return cancel (Function)

  Usage:
  const stop = AnimationLoop((delta, framecount, stop, time) => {
    console.log(delta, framecount);
    if(framecount > 99){
      stop();
    }
  }, 30);
*/
export const AnimationLoop = (fn, fps = null) => {
  if(typeof fn !== 'function'){
    throw new Error(`Invalid callback function.`);
  }
  let framecount = 0;
  let animation;
  let lastTime = performance.now();
  const cancel = _ => cancelAnimationFrame(animation)
  const loop = (time) => {
    animation = requestAnimationFrame(loop);
    const delta = time - lastTime;
    const now = performance.now();
    if(!fps || now - lastTime > 1000 / fps){
      fn.call(null, delta / 1000, framecount++, cancel, time);
      lastTime = time;
    }
  };
  animation = requestAnimationFrame(loop);
  return cancel;
};

let stopLoop;

// intersections
export const intersections = (() => {
  const store = writable([]);
  const { subscribe, set, update } = store;
  return {
    subscribe,
    add: (entry) => {
      update(_ => {
        // this is left in here to be able to to some cool parallax on 
        // scrolling handling at some later time
        let a = _;
        if(!stopLoop){
          // stopLoop = AnimationLoop((delta, frame, stop, time) => {
          //   // console.log(get(store))
          // });
        }
        return [..._, entry];
      });
    },
    remove: (entry) => {
      update((_) => {
        const i = _.indexOf(entry);
        if(i){
          _.splice(i, 1);
          if(!_.length && stopLoop){
            stopLoop()
            stopLoop = undefined;
          }
        }
        return _;
      });
    }
  }
})();