import { context, location, Router } from 'svelte-standalone-router';

// components 
import Work from '~/pages/work.svelte';
import Lab from '~/pages/lab.svelte';
import About from '~/pages/about.svelte';
import Contact from '~/pages/contact.svelte';
import Error from '~/pages/error.svelte';
import Videos from '~/pages/videos.svelte';

let prevPage = undefined;
const sameRouteCheck = (fn) => {
  if(location.pathname != prevPage){
    prevPage = location.pathname;
    fn.call(null);
  }
};

Router.linkBase = ''

const app = context({
  initial: location.pathname
});

// general error handler
app.catch((req, res) => {
  res.send(Error, { route: req.path });
});

app.use((req, res, next) => {
  // wrap in timeout to push it back to the stack
  setTimeout(() => window.scroll({top: 0, behavior: 'smooth'}), 10);
  next();
});

const render = (...args) => (req, res) => res.send(...args)

app.get('/', render(Work));
app.get('/lab', render(Lab));
app.get('/about', render(About));
app.get('/contact', render(Contact));

// videos
app.get('/videos', render(Videos));