<script>
  import { onMount } from 'svelte';

  import WorkItem from '~/components/work-item.svelte';
  import { intersections } from '~/stores/application';

  import intersectionObserver from '~/utils/intersectionObserver';

  let parent;
  onMount(() => {

    intersectionObserver({
      // on
      on: (entry) => {
        intersections.add(entry);
        entry.target.classList.add('show');
        entry.target.classList.add('once');
      },
      // off
      off: (entry) => {
        entry.target.classList.remove('show');
        intersections.remove(entry);
      }
    }, {targets: parent.querySelectorAll('.item'), threshold: 0})

  });

  export let items = [];
</script>

<section id="work" bind:this={parent}>
  {#each items as item}
    <WorkItem {...item} />
  {/each}

