<script>
  import { link, location } from 'svelte-standalone-router';
  
  // components
  import GithubIcon from '~/components/github-icon.svelte';
  import Switch from '~/components/switch.svelte';
  
  // language and theme
  import { lang, theme } from '~/stores/application';
  
  let nav;

  let state = false;
  let toggle = (e, s) => {
    e.stopPropagation();
    nav.classList.remove('prevent-transition');
    state = s;
  };
  
  // toggle on click outside
  document.addEventListener('click', (e) => {
    if(!nav.contains(e.target)){
      if(state){
        e.preventDefault();
        state = false;
      }
    }
  });

</script>

<button id="burger" on:click={e => toggle(e, !state)}>
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
    <path d="M6 12c0 1.657-1.343 3-3 3s-3-1.343-3-3 1.343-3 3-3 3 1.343 3 3zm9 0c0 1.657-1.343 3-3 3s-3-1.343-3-3 1.343-3 3-3 3 1.343 3 3zm9 0c0 1.657-1.343 3-3 3s-3-1.343-3-3 1.343-3 3-3 3 1.343 3 3z"/></svg>
</button>
<nav class:show={state} bind:this={nav} class="prevent-transition">
  <button on:click={toggle} class="close-navigation">
    <svg width="24" height="24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd">
      <path d="M12 11.293l10.293-10.293.707.707-10.293 10.293 10.293 10.293-.707.707-10.293-10.293-10.293 10.293-.707-.707 10.293-10.293-10.293-10.293.707-.707 10.293 10.293z"/></svg>
  </button>
  <Switch change={ theme.toggle } checked={$theme} />
  <div class="social">
    <GithubIcon />
    <a href="{$lang.header.github.slug}">{$lang.header.github.label}</a>
  </div>
  <div class="navigation">
    {#each $lang.header.navigation as item}
      <a href="{item.slug}" use:link class:active={$location == item.slug} on:click={e => toggle(e, false)}>{item.label}</a>
    {/each}
  </div>
</nav>